.about {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    height: 90vh;
    

    .about-background {
            background: url('https://i.ibb.co/7rrfDKC/paulo-doi-6u-TQmtqc-Azs-unsplash-1.jpg') no-repeat center 38%;
            background-size: cover;
            height: 45%;
        }

    .about-text-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 55%;
        padding: 0 5rem;
       
        

        h2 {
            margin: 0;
            font-size: 4.5rem;
            font-weight: 400;

            @media only screen and (max-width: 620px) {
                font-size: 3rem;
            }
        }    
    
        p {
            font-size: 1.2rem;

            @media only screen and (max-width: 720px) {
                font-size: 1rem;
            }
        }

        @media only screen and (max-width: 620px) {
            padding: 0 1rem;
        }
    }    

}

